import React, { useRef, useState, useEffect } from 'react';
import { Helmet } from 'react-helmet';
import './styles/index.scss';
import Background from './components/background/Background';

const IndexPage = () => {
  const containerRef = useRef<HTMLHeadingElement>(null);
  const titleRef = useRef<HTMLHeadingElement>(null);

  function scrollTo(pixels: number) {
    if (containerRef && containerRef.current) {
      containerRef.current.scrollTo(0, pixels);
    }
  }

  useEffect(() => {
    if (titleRef && titleRef.current) {
      titleRef.current.style.opacity = '1';
      titleRef.current.style.marginTop = '0';
      window.scrollTo(0, 500)
    }
  });

  return (
    <main>
      <Helmet>
        <title>Balzac World </title>
        <meta property="og:title" content="Balzac World " />
        <meta property="og:description" content="Best portfolio ever seen" />
        <meta property="og:url" content="https://www.balzac.dev/" />
        <meta property="og:site_name" content="Balzac World " />
        <meta name="viewport" content="width=device-width, initial-scale=1, maximum-scale=1, user-scalable=no, viewport-fit=cover" />
        <script async src="https://pagead2.googlesyndication.com/pagead/js/adsbygoogle.js?client=ca-pub-8519454460902262"
          crossorigin="anonymous"></script>
      </Helmet>
      <Background />
      <div className="container" ref={containerRef}>
        <section className="title" ref={titleRef}>
          <h1>Welcome to Balzac's world </h1>
          <span onClick={() => scrollTo(window.innerHeight)}>Scroll down </span>
        </section>
        <section className="aboutme">
          <h1>A french developer based in Paris  </h1>
          <h2>Currently working at <a href="https://dreem.com/">Dreem</a> as a front-end developer</h2>
          <footer>
            <a href="https://github.com/balzacbdmt">GitHub</a>
            <a href="https://www.linkedin.com/in/balzac-baudemont-796a011b1">LinkedIn</a>
            <a href="https://www.instagram.com/balzac.ode/">Instagram</a>
            <a href="mailto:balzac.baudemont@gmail.com">E-mail</a>
          </footer>
        </section>
      </div>
    </main>
  );
};

export default IndexPage;
